import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import * as styles from "./static.module.scss"

export default function About({ data }) {
  const siteTitle = data.site.siteMetadata.title
  const author = data.site.siteMetadata.author

  return (
    <Layout
      siteTitle={siteTitle}
      author={author}
      pageTitle="About"
      metaDescription="About Me"
    >
      <div className={styles.main}>
        <h1>Junya Morioka</h1>
        <p>情報系の大学院生です</p>
        <p>私が学習した技術についてのメモを掲載します．</p>
        <p>
          本サイトはReact製の静的サイトジェネレーター(SSG)であるGatsby.jsで生成したサイトをGithub
          Pagesで公開しています． シンプルなサイト構成を心がけて運用します．
        </p>
        <div className="skill">
          <h2>Skill</h2>
          <ul style={{ listStyle: "none" }}>
            <li>Python</li>
            <ul>
              <li>PyTorch</li>
              <li>TensorFlow</li>
            </ul>
            <li>JavaScript</li>
            <ul>
              <li>React</li>
              <li>Gatsby.js</li>
            </ul>
            <li>AWS</li>
            <li>Swift</li>
            <li>Ruby</li>
            <li>C</li>
            <li>Java</li>
          </ul>
        </div>
        <div className="contact">
          <h2>Contact</h2>
          <p>
            本サイトの内容について連絡がある場合や、私について質問がある場合は以下の連絡先までお願いいたします。
          </p>
          <p>mjun あっと mjunya.com</p>
        </div>
      </div>
    </Layout>
  )
}

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
        author
      }
    }
  }
`
